@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body{ font-family: 'Roboto', sans-serif !important;}
.activesd{color:#de6320 !important;}
.bg_topnav {
  background: #0f0f16;
  min-height: 40px;
  color: #fff;
  font-size: 14px;
  padding: 5px 5px; }

.bg_topnav span {
  padding: 1px 2px;
  display: inline-block; }

span.firstsp {
  padding-right: 17px; }

.bg_topnav i {
  font-size: 20px;
  margin: 2px;
  position: relative;
  top: 2px; }

.bg_topnav span a {
  color: #fff; }

.bg_topnav span a:hover {
  color: #de6320; }

.bg_theme {
  background: #fff;
  min-height: 80px;
  color: #fff;
  font-weight: 400;
  font-family: "Roboto", sans-serif !important; }

.bg_theme nav {
  padding-top: 4px;
  font-size: 14px;
  font-weight: bold; }

.navbar-nav a {
  color: #716b6b !important; }

.bg_theme nav a:hover {
  color: #de6320 !important; }

a.navbar-brand p {
  color: #716b6b;
  font-weight: normal;
  font-size: 29px;
  position: relative;
  top: 7px; }

a.navbar-brand p strong {
  color: #de6320;
  font-weight: bold; }

@media only screen and (max-width: 768px) {
  .bg_theme nav {
    padding: 0px; }
  .bg_theme {
    min-height: 68px; } }

.slider_theme {
  background: url(../../static/media/slider_bg.13a6b328.jpg);
  max-height: 647px;
  color: #fff;
  font-weight: 400;
  font-family: "Roboto", sans-serif !important;
  background-size: 100% auto;
  position: relative;
  z-index: 10;
  padding: 12% 18% 20%; }

.slider_theme:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.65);
  z-index: -1; }

h1 {
  color: #fff;
  font-size: 3.8em;
  font-weight: bold;
  padding-top: 5%; }

.carousel {
  position: relative;
  width: 100%; }

h1 + p {
  color: #fff;
  font-size: 14px; }

.basedTM {
  background: #de6320;
  border: 1px solid #de6320;
  margin: 15px 0;
  padding: 14px 53px; }

.basedTM:hover {
  background: #fff;
  color: #de6320; }

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 1.8em; } }

.widgets_section {
  background: #f6f6f6;
  padding: 0px 6%; }

.widgets_section1 {
  background: #f6f6f6;
  padding: 4% 6%; }

.widgets_section > div {
  margin-top: -62px;
  position: relative;
  z-index: 999; }

.widgets_section1 .jumbotron {
  background: transparent; }

.widgets_section .card-title {
  font-size: 4em !important;
  color: #de6320;
  font-weight: bold;
  border-bottom: 1px solid #716b6b;
  padding: 8px 0; }

.widgets_section .card {
  border-radius: 15px; }

.widgets_section p.card-text {
  font-weight: bold;
  font-size: 20px;
  color: #716b6b; }

.widgets_section1 .jumbotron {
  padding: 0px; }

.widgets_section1 .jumbotron h2 {
  font-size: 2.6em;
  font-weight: bold;
  margin-bottom: 21px; }

.widgets_section1 .jumbotron p {
  font-size: 14px;
  color: #716b6b; }

.widgets_section1 .img-fluid {
  margin-bottom: 20px; }

@media only screen and (max-width: 768px) {
  .widgets_section > div {
    margin-top: 20px; } }

.clientPreview {
  background: #fff;
  padding: 2% 0 0; }

.clientPreview [role="button"] {
  display: none; }

h5, p {
  color: #555; }

.clientText h2 {
  color: #0f0f16;
  font-size: 2.5em;
  font-weight: bold; }

.clientText p {
  color: #0f0f16;
  font-size: 14px; }

.clientPreview .card {
  background: #fefefe; }

.clientPreview .card span {
  float: left;
  text-align: left;
  width: 100%;
  color: #716b6b;
  font-size: 14px;
  position: relative;
  top: -10px; }

.clientText {
  padding: 0% 20%; }

.clientPreview .carousel-caption {
  position: static !important;
  margin: 0 auto;
  max-width: 71%; }

.clientPreview .carousel-indicators li {
  background-color: #716b6b; }

.clientPreview .carousel-indicators li.active {
  background-color: #de6320; }

.clientPreview .carousel-indicators {
  bottom: -30px; }

@media only screen and (max-width: 768px) {
  .clientText {
    padding: 0% 5%; }
  .clientPreview .carousel-caption {
    max-width: 92%; } }

footer {
  background: #0f0f16;
  min-height: 450px;
  position: relative;
  margin-left: -15px;
  margin-right: -15px;
  padding-left: 15px;
  padding-right: 15px; }

.copyright {
  background: #de6320;
  float: left;
  width: 100%;
  padding: 10px;
  position: absolute;
  bottom: 0;
  text-align: center; }

.custom, .custom a {
  background: transparent;
  color: #fff !important; }

.custom p {
  color: #fff !important;
  font-size: 14px; }

.custom .card-title {
  font-size: 1.6em;
  font-weight: 500; }

.custom span p {
  font-size: 12px; }

.custom.card span {
  position: relative;
  border-bottom: 1px solid #555;
  width: 100%;
  float: left; }

.icon_font {
  position: absolute;
  width: 50px;
  height: 50px;
  background: #de6320;
  text-align: center;
  border-radius: 161px;
  font-size: 20px;
  line-height: 51px;
  left: -2px; }

.custom.card span {
  padding-left: 62px;
  padding-top: 7px; }

.custom.card span:last-child {
  border: none; }

.custom.card span strong {
  font-size: 18px;
  font-weight: 400; }

.custom.card ul li a {
  padding: 13px 0;
  float: left;
  font-size: 14px;
  width: 100%;
  border-bottom: 1px solid #555; }

.custom.card ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px; }

.custom.card ul li:last-child a {
  border: none; }

.custom.card ul li a:hover {
  color: #de6320 !important; }

.custom.card form input, .custom.card form textarea {
  background: transparent;
  border: none;
  border-bottom: 1px solid #555;
  border-radius: 0px;
  padding: 3px 0;
  font-size: 14px; }

.submit_btn {
  width: 100%;
  background: #de6320;
  border-color: #de6320 !important;
  padding: 11px 0px;
  font-size: 17px;
  border-radius: 0px; }

.submit_btn:hover {
  color: #de6320 !important;
  background: #fff; }

footer .row:first-child {
  padding: 24px 58px 73px !important; }

.copyright p {
  color: #fff;
  margin: 8px 0;
  font-size: 14px; }

.copyright a {
  color: #fff;
  text-decoration: underline; }

@media only screen and (max-width: 768px) {
  footer .row:first-child {
    padding: 24px 5px 73px !important; } }

