@import "../sass/variables.scss";

footer{ background: $primaryColor; min-height: 450px;
    position: relative;
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 15px;
    padding-right: 15px;}
.copyright {
    background: $secondaryColor;
    float: left;
    width: 100%;
    padding: 10px;
    position: absolute;
    bottom: 0;
    text-align: center;
    
}
.custom,  .custom a{ background: transparent; color: $textcolorColor !important;}
.custom p{color: $textcolorColor !important; font-size: $mainContentSize; }
.custom .card-title {
    font-size: 1.6em;
    font-weight: $primaryFontWeight_5;
}.custom span p {
    font-size: 12px;
}
.custom.card span {
    position: relative;
    border-bottom: 1px solid $headingColor;
    width: 100%;
    float: left;
}
.icon_font {
    position: absolute;
    width: 50px;
    height: 50px;
    background: $secondaryColor;
    text-align: center;
    border-radius: 161px;
    font-size: 20px;
    line-height: 51px;
    left: -2px;
}
.custom.card span {
    padding-left: 62px;     padding-top: 7px;
}
.custom.card span:last-child{ border: none;}
.custom.card span strong {
    font-size: 18px;
    font-weight: $primaryFontWeight_4;
}


.custom.card ul li a {
    padding: 13px 0;
    float: left;     font-size: $mainContentSize;
    width: 100%;
    border-bottom: 1px solid $headingColor;
}
.custom.card ul {
    list-style-type: none;
    margin: 0px;
    padding: 0px;
}
.custom.card ul li:last-child a{ border: none;}
.custom.card ul li a:hover{ color:$secondaryColor !important;}

.custom.card form input, .custom.card form textarea {
    background: transparent;
    border: none;
    border-bottom: 1px solid $headingColor;
    border-radius: 0px;
    padding: 3px 0;     font-size: $mainContentSize;
}
.submit_btn{ width: 100%; background: $secondaryColor; border-color:$secondaryColor !important;padding: 11px 0px;
    font-size: 17px;
    border-radius: 0px;
}

.submit_btn:hover{color:$secondaryColor !important; background: $textcolorColor; }
footer .row:first-child {
   padding: 24px 58px 73px !important;
}
.copyright p{color: $textcolorColor;  margin: 8px 0; font-size: $mainContentSize;}
.copyright a{color: $textcolorColor; text-decoration: underline; }
@media only screen and (max-width: 768px) {
footer .row:first-child {
    padding: 24px 5px 73px !important;
}
}
