@import "../sass/variables.scss";

.clientPreview{
    background: $textcolorColor;
    padding: 2% 0 0;
    
}
.clientPreview .carousel-item {
   
}.clientPreview [role="button"] {
    display: none;
}
h5, p{color: $headingColor;}
.clientText h2 {
    color: $primaryColor;
    
    font-size: 2.5em;
    font-weight: $primaryFontWeight_B;
}
.clientText p{  color: $primaryColor;
    
    font-size: $mainContentSize;}
    .clientPreview .card {
        background: #fefefe;
    }
    .clientPreview .card span {
        float: left;
        text-align: left;
        width: 100%;
        color: $navheadingColor;
        font-size: 14px;
        position: relative;
        top: -10px;
    }.clientText {
        padding: 0% 20%;
    }
    .clientPreview .carousel-caption{ position: static !important;
        margin: 0 auto;
        max-width: 71%;}
    .clientPreview .carousel-indicators li{background-color:  $navheadingColor;}
    .clientPreview .carousel-indicators li.active{background-color:  $secondaryColor;}
    .clientPreview .carousel-indicators{    bottom: -30px; }
@media only screen and (max-width: 768px) {
    .clientText {
        padding: 0% 5%;
    } .clientPreview .carousel-caption{     max-width: 92%;}
}