// import starting from the src/ folder
@import "../sass/variables.scss";

.widgets_section {
   background: $lightBG;
    padding: 0px 6%;
}
.widgets_section1 {
    background: $lightBG;
     padding: 4% 6%;
 }
.widgets_section>div {
    margin-top: -62px;
    position: relative;
    z-index: 999;
}
.widgets_section1 .jumbotron {
    background: transparent;
}
.widgets_section .card-title {
    font-size: 4em !important; color: $secondaryColor;
    font-weight: $primaryFontWeight_B;
    border-bottom: 1px solid $navheadingColor;
    padding: 8px 0;
}.widgets_section .card {
    border-radius: 15px;
}
.widgets_section p.card-text {
    font-weight: $primaryFontWeight_B;
    font-size: 20px;
    color: $navheadingColor;
}
.widgets_section1 .jumbotron {
    padding: 0px;
}

.widgets_section1 .jumbotron h2 {
    font-size: 2.6em;
    font-weight: bold;
    margin-bottom: 21px;
}
.widgets_section1 .jumbotron p{ font-size: $mainContentSize; color: $navheadingColor;}
.widgets_section1 .img-fluid {
   
    margin-bottom: 20px;
}
@media only screen and (max-width: 768px) {
    .widgets_section > div{     margin-top: 20px;}
}