// import starting from the src/ folder
@import "../sass/variables.scss";

.slider_theme{
  background:url('../images/slider_bg.jpg');
  max-height:647px;
  color: $textcolorColor; font-weight: $primaryFontWeight_4;
  font-family: $primaryFont;
  background-size: 100% auto; position: relative;  z-index: 10; padding: 12% 18% 20%;
};

.slider_theme:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background :$transBG;     z-index: -1;
}

h1 { color:$textcolorColor; font-size: 3.8em; font-weight: $primaryFontWeight_B;     padding-top: 5%; }
.carousel {
    position: relative;
    width: 100%;
}
h1 + p {color: $textcolorColor; font-size: $topNavContentSize }
.basedTM{ background: $secondaryColor; border: 1px solid $secondaryColor; margin: 15px 0; padding: 14px 53px;}
.basedTM:hover{  background: $textcolorColor; color:$secondaryColor; }
@media only screen and (max-width: 768px) {
    h1{ font-size: 1.8em;}
}