// import starting from the src/ folder
@import "../sass/variables.scss";

.bg_topnav {
  background: $primaryColor;
  min-height:40px;
  color: $textcolorColor;
  font-size: $topNavContentSize;
  padding: $commanPadding;
};
.bg_topnav span {
  padding: 1px 2px;
  display: inline-block;
}
span.firstsp {
  padding-right: 17px;
}
.bg_topnav i { font-size: $topNavIconSize; margin: 2px; position: relative; top:2px;}
.bg_topnav span a{ color:$textcolorColor; }
.bg_topnav span a:hover{ color: $secondaryColor;}