// import starting from the src/ folder
@import "../sass/variables.scss";

.bg_theme{
  background: $textcolorColor;
  min-height:80px;
  color: $textcolorColor; font-weight: $primaryFontWeight_4;
  font-family: $primaryFont;
};
.bg_theme nav {
  padding-top: 4px;
  font-size: $topNavContentSize; 
  font-weight: $primaryFontWeight_B;
}
 .navbar-nav a{
color: $navheadingColor !important;}
 .bg_theme nav a:hover{color:$secondaryColor !important; }
a.navbar-brand p {
  color: $navheadingColor;
  font-weight: $primaryFontWeight_N;
  font-size: 29px;
  position: relative;
  top: 7px;
}
a.navbar-brand p strong {
  color: $secondaryColor;
font-weight: $primaryFontWeight_B;
}
@media only screen and (max-width: 768px) {
  .bg_theme nav {
    padding: 0px;
} .bg_theme{ min-height: 68px;}
}


